onmessage = function (e) {
  let filtered = e.data.input;
  if (e.data.filter_type === 'time') {
    filtered = time(e.data.time);
  }
  if (e.data.filter_type === 'unique') {
    filtered = unique();
  }
  if (e.data.filter_type === 'searchComment') {
    filtered = comment(e.data.word);
  }
  if (e.data.filter_type === 'searchName') {
    filtered = name(e.data.word);
  }
  if (e.data.filter_type === 'tag') {
    filtered = tag(e.data.people);
  }
  if (e.data.filter_type === 'reaction') {
    filtered = reaction(e.data.type);
  }

  function unique() {
    let output = [];
    let keys = [];
    output = filtered.filter(item => {
      if (keys.indexOf(item.from.id) === -1) {
        keys.push(item.from.id);
        return item;
      }
    });
    return output;
  }

  function comment(word) {
    return filtered.filter(item => {
      return item.message.indexOf(word) >= 0;
    });
  }
  function name(word) {
    return filtered.filter(item => {
      return item.from.name.indexOf(word) >= 0;
    });
  }

  function tag(people) {
    return filtered.filter(item => {
      return item.message_tags >= people;
    });
  }

  function reaction(type) {
    if (type === 'all'){
      return filtered;
    }else{
      return filtered.filter(item => {
        return item.type == type;
      });
    }
  }

  function time(t){
    return filtered.filter(item => {
      return (moment(item.created_time) >= moment(t.start)) && (moment(item.created_time) <= moment(t.end))
    });
  }

  postMessage(filtered);
}



